.about{
    padding-bottom: 48px;
}
.about-top{
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-photo{
    padding-left: 50px;
}
.cover{
    height: 302px;
}
.about-info{
    font-size: 24px;
}
.info-name{
    font-size: 32px;
    color: blueviolet;
}

@media only screen and (max-width:720px){
    .about-top{
        flex-direction: column;
    }
    .cover{
        height: 200px;
        margin-top: 10px;
    }
    .about-info{
        font-size: 20px;
    }
    .info-name{
        font-size: 26px;
    }

}